import React from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../../../app/store';

import {
    Holder,
    UserData,
    UserPic,
    UserPersonal,
    Row,
    Name,
    SmallStatictics,
    DataField,
    DataHihglight,
    DataNormal, 
    UserIcon,
    RecentEvent,
    Text,
    Desc,
    Event,
    Header,
    SubHeader,
    LocationHolder,
    ButtonsField,
    Button
} from './Profile.elements';

const Profile = () => {
    const {username} = useSelector((state: RootState) => state.auth)
    const {recentEvent} = useSelector((state: RootState) => state.events.dashboard)

    const confirmationHanlder = () => {
        //check user localization
        //if user's loc === event loc => participany amount increase
    }

    const cancelHanlder = () => {
        //delete event
    }

    const findHanlder = () => {
        //redirect to event page
    }

    return (
        <Holder>
            <UserData>
                <Row>
                    <UserPic>
                        <UserIcon/>
                    </UserPic>
                </Row>
                <Row>
                    <UserPersonal>
                        <Name>{username ? username : 'CleverTiger069'}</Name>
                        <SmallStatictics>
                            <DataField>
                                <DataHihglight>2</DataHihglight>
                                <DataNormal>created</DataNormal>
                            </DataField>
                            <DataField>
                                <DataHihglight>100</DataHihglight>
                                <DataNormal>participated</DataNormal>
                            </DataField>
                        </SmallStatictics>
                    </UserPersonal>
                </Row>
            </UserData>

            <RecentEvent>
                <Text>Current event</Text>
                <Event>
                    {recentEvent ? (
                        <LocationHolder>
                            <Header>{recentEvent.title}</Header>
                            <SubHeader>{recentEvent.city}, {recentEvent.street}</SubHeader>
                        </LocationHolder>
                    ) : (
                        <LocationHolder>
                            <Header>Event not found!</Header>
                        </LocationHolder>
                    )}
                    {/* <Desc>{recentEvent.description}</Desc> */}

                    <ButtonsField>
                        {recentEvent ? <Button ButtonType='confirm' onClick={confirmationHanlder}>On the spot!</Button> : <Button ButtonType='confirm' onClick={findHanlder}>Find Events</Button>}
                        {recentEvent && <Button ButtonType='cancel' onClick={cancelHanlder}>Disclaim.</Button>}
                    </ButtonsField>
                </Event>
            </RecentEvent>
        </Holder>
    );
};

export default Profile;